 <!--Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .page__hd {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 99;
    background-color: transparent;
     -webkit-transition: background-color .3s;
     -moz-transition: background-color .3s;
     -ms-transition: background-color .3s;
     -o-transition: background-color .3s;
    transition: background-color .3s;

    &.fixed {
      background-color: #f2f2f2;
       -webkit-box-shadow: 0 0 5px 1px rgba(0,0,0,.3);
       -moz-box-shadow: 0 0 5px 1px rgba(0,0,0,.3);
      box-shadow: 0 0 5px 1px rgba(0,0,0,.3);
    }
  }

  .page__hd-main {
    width: 1220px;
    padding: 0 10px;
    margin: 0 auto;
    /*text-align: right;*/

    .page__hd__lt {
      float: left;
      height: 60px;
      line-height: 60px;

      a {
        color: #4c6aff;
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        align-items: center;
      }

      .logo {
        display: inline-block;
        width: 30px;
        height: 30px;
        background: url("../assets/logo.png") no-repeat center;
        margin-right: 20px;
      }
    }

    .page__hd__rt {
      float: right;
      background-color: transparent;

      .user-avatar {
        display: inline-block;
        width: 40px;
        height: 40px;
         -webkit-border-radius: 50%;
         -moz-border-radius: 50%;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 10px;
        /*border: 1px solid red;*/

        img {
          width: 100%;
          height: 100%;
          vertical-align: top;
        }
      }
    }
  }

  .page__bd {
    position: relative;
    /*padding-top: 240px;*/
    overflow: hidden;
    padding-bottom: 0;

    .head-bg {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      height: 850px;
      background: url("../assets/index_bg.png") no-repeat;
    }

    .el-main {
      position: relative;
    }

    .page__bd-main {
      /*width: 1240px;*/
      margin: 0 auto;
    }
  }

  .el-menu--horizontal {
    border-bottom: none;
  }

  .transitionRouter-enter-active {
    transition: all 4s;
  }

  .transitionRouter-leave-active {
    transition: all 6s;
  }

  .transitionRouter-enter,
  .transitionRouter-leave {
    transform: translate3d(0, 100%, 0);
  }

</style>

<template>
  <el-container class="page" id="page">
    <el-header class="page__hd" v-bind:class="{ fixed: isPageHdFixed}">
      <div class="page__hd-main clearfix">
        <div class="page__hd__lt">
          <router-link to="{name:indexPage}">
          <!--<router-link href="javascript:;" @click="goHome">-->
            <span class="logo"></span>
            侵权管理系统
          </router-link>
        </div>
        <el-menu class="page__hd__rt" mode="horizontal">
          <el-submenu index="1" class="submenu-list">
            <template slot="title"><span class="user-avatar"><img src="../assets/logo.png" alt=""></span>admin
            </template>
            <el-menu-item index="1-1" @click="exit">注销</el-menu-item>
            <el-menu-item index="1-2">个人资料</el-menu-item>
          </el-submenu>
        </el-menu>
      </div>
    </el-header>
    <el-main class="page__bd">
      <div class="head-bg"></div>
      <div class="page__bd-main">
        <!--<transition name="el-zoom-in-bottom">-->
          <!--<transition name="bounceInUp" :duration="{ enter: 300, leave: 200 }">-->
          <!--<router-view></router-view>-->
        <!--</transition>-->
      </div>
    </el-main>
    <el-footer></el-footer>
  </el-container>
</template>

<script>
  export default {
    name: 'Index',
    data() {
      return {
        msg: 'Welcome index',
        isPageHdFixed: false
      }
    },

    methods: {
      exit() {
        window.localStorage.removeItem('userAuth');
        this.$router.push({name: 'LoginPage'})
      },
      handleScroll () { //改变元素#searchBar的top值
        let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
        let offsetTop = document.querySelector('.page').offsetTop;

        if (scrollTop > 10) {
          this.isPageHdFixed = true
        } else {
          this.isPageHdFixed = false
        }
      },
    },
    mounted() {
      if (!window.localStorage.getItem('userAuth')) {
        this.$router.push({name: 'LoginPage'})
      }
      window.addEventListener('scroll', this.handleScroll)
    }
  }
</script>



